@use 'base/mixins' as m;
@use 'base/variables' as v;

.blog__grid{
    gap: 4rem;

    @include m.tablet { 
        @include m.grid(2, 1fr);
    }

    @include m.desktop { 
        @include m.grid(4, 1fr);
    }

    &--pagina{ //Modificador de la pagina html
        @include m.desktop { 
            @include m.grid(3, 1fr);
        }
    }

}

.entrada{
    max-width: 80rem;
    margin: 0 auto;

    &__contenido{
        padding: 2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    &__titulo{
        font-weight: 500;
        font-size: 2.8rem;
        margin: 0;
        line-height: 1.2;
        text-align: center;
    }

    &__fecha{
        color: v.$primario;
        font-size: 1.6rem;
    }

    &__enlace{
        @include m.boton(v.$negro,v.$primario);
        font-size: 1.2rem;
        font-weight: 900;
   
        &:hover{
           color: v.$blanco;
        }
    }

    &__texto{
        display: -webkit-box;
        //Este sera un display para que crezca y rellene la caja
        margin: 0;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 4;
        overflow: hidden;
        font-size: 2rem;
    }
}