@use 'base/mixins' as m;
@use 'base/variables' as v;

.productos{

    &__grid{
        display: grid;
       gap: 4rem;

       @include m.tablet { 
           @include m.grid(2, 1fr);
       }
       @include m.desktop { 
           @include m.grid(3, 1fr);
       }
    }
}

.producto{
   display: grid;
   grid-template-columns: 2fr 3fr; 
   column-gap: 1rem;
   align-items: center;

   &__nombre{
    font-size: 3rem;
    text-transform: uppercase;
   }

   &__descripcion{
    font-size: 1.6rem;

   }

   &__precio{
    color: v.$primario;
    font-size: 6rem;
    font-weight: 900;
    margin: 0;
   }

   &__enlace{
     @include m.boton(v.$negro,v.$primario);
     font-size: 1.2rem;
     font-weight: 900;

     &:hover{
        color: v.$blanco;
     }
   }
}