@use 'variables' as v; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-size: 2rem;
    font-family: v.$fuentePrincipal;
    line-height: 1.5;
}
[class*="contenedor"]{ //Buscara todas las clases que finalizan en contenedor
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
[class*="heading"]{
    font-size: 4rem;
    text-align: center;
    font-weight: 900;
    color: v.$primario;
    margin: 5rem 0;
}
img {
    max-width: 100%;
}
ul {
    padding:0;
    list-style: none;
    margin: 0;
}
a{
    text-decoration: none;
    color: v.$blanco;
}

