@use 'base/mixins' as m;
@use 'base/variables' as v;

.navegacion{
    display: flex;
    gap: 2rem;
    margin-top: 5rem;
    position: relative;
    z-index: 1;

    @include m.tablet { 
        flex-direction: row;
        margin-top: 0rem;
    }

    &__enlace{
        text-transform: uppercase;
        padding: .5rem;
        font-size: 1.8rem;
        font-weight: 700;
        transition-property: background-color;
        transition-duration: .3s;

        &:hover{
            background-color: v.$primario;
        }

        &--modificador{
            background-color: v.$gris;
            color: v.$negro;
            border-radius: .5rem;
        }

    }
}