@use 'base/mixins' as m;
@use 'base/variables' as v;

.producto{

    &__formulario{
        display: flex;
        flex-direction: column;
    }

    &__label{
        padding: 2.2rem;
        margin-right: 1rem;
    }

    &__cantidad{ 
        border: 1px solid v.$primario;
        padding: 1rem;
        text-align: center;
    }
    &__cantidad:focus-visible{//Con este cambiara de color una vez seleccionado 
        outline: 1px solid v.$primario;
        border: none;
    }

    &__agregar-carrito{
        margin-top: 3rem;
        display: block;
        background-color: v.$primario;
        text-decoration: none;
        text-transform: uppercase;
        border: none;
        text-align: center;
        padding: 2rem 5rem;
        color: v.$blanco;
        font-size: 1.8rem;
        font-weight: 700;
        transition-property: background-color;
        transition-duration: .3s;

        &:hover{
            background-color: v.$negro;
            cursor: pointer;
        }
    }
}