@use 'base/mixins' as m;
@use 'base/variables' as v;

.header{
    background-image: linear-gradient(to right, rgb(0 0 0 / .7), rgb(0 0 0 / .7)), url(../img/header.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    padding: 5rem 0;
    position: relative;



    &__barra{
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet { 
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__logo{
        width: 30rem; //El logo se har치 mas peque침o
    }

    &__guitarra{
        display: none;

        @include m.desktop { 
            display: block; //Para que se muestre la guitarra
            position: absolute;
            right: 0;
            bottom: 0;
            
            //LONGHAND
            // animation-name: mostrarImagen;
            // animation-duration: 2s;
            // animation-delay: 0s; //Despues de un segundo empiece la animacion
            // animation-timing-function: ease-in-out;
            // animation-iteration-count: 1; //Cuantas veces sucedera la animation
       
            //SHORTHAND
            animation: 1s ease-in-out 0s 1 mostrarImagen;
        }
        

        @keyframes mostrarImagen{
            0%{
                opacity: 0;
                transform: translateX(-10rem); //Aparezca menos 10 px
            }

            50%{
                opacity: 0;
            }

            100%{
                opacity: 1;
                transform: translateX(0rem); //Se acomode en su posici칩n original
            }
        }

    }
}

