@use 'base/mixins' as m;
@use 'base/variables' as v;

.modelo{
 color: v.$blanco;
 margin-top: 5rem;
 text-align: center;

 @include m.tablet { 
     text-align: center;
    
 }

 @include m.desktop { 
     text-align: left;
     width: 50%;
 }

    &__nombre{
        font-size: 8rem;
    }

    &__precio{
        font-size: 6rem;
        color: v.$primario;
        font-weight: 900;
        line-height: 0;
    }

    &__descripcion{
        position: relative;
        z-index: 1;
    }

    &__enlace{
    @include m.boton(v.$primario, v.$blanco);
    }
}