@use 'base/mixins' as m;
@use 'base/variables' as v;

.cursos{
    background-image: linear-gradient(to right, rgb(0 0 0 / .65), rgb( 0 0 0 / .65)),
    url(../img/cursos_bg.jpg);
    padding: 10rem 0;
    margin-top: 10rem;
    background-size: cover;
    background-position: 50%;

    &__contenido{
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet { 
            grid-column: 2 / 3;
        }
    }

    &__grid{
        @include m.desktop { 
            @include m.grid(2, 1fr);
        }
    }
    &__texto{
        color: v.$blanco;
        text-align: center;
        font-size: 2.4rem;
    }

    &__enlace{
         @include m.boton(v.$blanco,v.$primario);
         font-size: 1.4rem;
         font-weight: 700;

         &:hover{
            color: v.$blanco;
         }
    }
}